import { Component, OnInit } from '@angular/core';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { MatDialog } from '@angular/material/dialog';
import { MatSnackBar } from '@angular/material/snack-bar';
import { Router } from '@angular/router';
import { ApiService } from 'app/shared/services/api.service';
import { DataService } from 'app/shared/services/data.service';
import { DialogService } from 'app/shared/services/Dialog/dialog.service';
import { LocalStoreService } from 'app/shared/services/local-store.service';
import { Location } from '@angular/common';
import { otpModel } from 'app/shared/models/otp.model';

@Component({
  selector: 'app-loan-repayment-service',
  templateUrl: './loan-repayment-service.component.html',
  styleUrls: ['./loan-repayment-service.component.scss']
})

export class LoanRepaymentServiceComponent implements OnInit {
  title = "Loan Payment";
  custValidation: string;
  loanRepaymentForm: FormGroup;
  custPref = "N";
  entityCode="ENTCD";
  bankCode = "BNKC" ;
  tokenBranch = "KN" ;
  oTpModel = new otpModel();

  constructor(private formBuilder: FormBuilder,private ls: LocalStoreService,private apiService:ApiService,
    private router: Router,  private dialogService : DialogService,  private dialog: MatDialog, private snack: MatSnackBar,
    private _location: Location,private dataService:DataService) { }

  ngOnInit(): void {
    localStorage.setItem('chq_page', this.title);
    this.custValidation = localStorage.getItem("customerValidation");
    this.loanRepaymentForm = this.formBuilder.group({

     loanInfo:[],
     paymentInfo:[],
      denomination:[],
      entityCode:[this.entityCode,[Validators.required]],
      bankCode:[this.bankCode,[Validators.required]],
      tokenBranch:[this.tokenBranch,[Validators.required]],
      customerCategory:[this.custPref,[Validators.required]],
      tokenNumber:['', [Validators.required]],
      productCode:['CHW',
        Validators.required
    ],
      tokenOrigin :['Kiosk',
        Validators.required
      ],
      estimatedWaitTime:['', [Validators.required]],
      narrative:['', [Validators.required]],
    });
  }
  public redirectToHome(): void {
    this.router.navigateByUrl('/others/kioskHome');
  }
  public redirectToHome1(): void {
     console.log("Redirect")
    this.router.navigateByUrl('/others/kioskHome');
  }
  back() {
   
    this._location.back();
  }
  onSubmit(event){
    console.log(event);
  }

}
