import { Component, OnInit } from '@angular/core';
import { FormControl, FormGroup, Validators } from '@angular/forms';
import { ApiService } from 'app/shared/services/api.service';
import { Subscription } from 'rxjs';

@Component({
  selector: 'app-payment-information',
  templateUrl: './payment-information.component.html',
  styleUrls: ['./payment-information.component.scss']
})
export class PaymentInformationComponent implements OnInit {
  checked:boolean=true;
  paymentForm:FormGroup;
  showAccountNum: boolean = true;
  showAccoutType: boolean = true;
  ifQRvariable = false;
  index: string[];
 
  productCode = 'CHW';
  tokenOrigin = 'Kiosk';
  custPref = 'N';
  entityCode = 'ENTCD';
  bankCode = 'BNKC';
  tokenBranch = '1234';
  accountNum: string;
  accountBal: any;
  phoneNumber: string;
  errorStatus: number;
  custAccountData: any;
  countryValues: any;
  accounttypeArray = [];
  idList = [];
  noList: any[];
  accNum: any;
  totalAmount: any;
  transamount: any;
  a: any;
  accNumber: any;
  appointmentId: any;
  tokResponse: any;
  tokenNum: any;
  estimatedTime: any;
  transAmt: any;
  balance: string;
  enableContinue: boolean = true;
  isedit: boolean = true;
  accountCurrency1: any;
  curr: string;
  transAmount: string;
  currencyImage: any = '';
  custID: any;
  qrData: any;
  customerID: any;
  selected: string;
  userName: string;
  branchNameFromLogin: string;
  branchName: string;
  arrayAccountBranch: any;
  trasactionBranches: any;
  branchData: any;
  branch: any;
  resultStatus: any;
  // accountNoList[]: any[];
  accountNoList = [];
  selectedAccountInfo: any;
  custValidation: string;
  accountNumber: string;
  custmerID: any;
  phonenum: any;
  cashwithDrwValues: any;
  cashwithDrwform: any;
  cashwithDrwType: any;
  enableBtn: boolean = true;
  enableBtn1: boolean = false;
  accountNum1: any[];
  filteredAccoun: any[];
  accountDropdown: boolean = true;
  flag1: boolean = false;
  displayFlag:boolean = true;
  denomRequired: any = '';
  hideDetails: boolean = true;
  hideAccNum: boolean = true;
  qrBranchCode: any;
  custNameFromLogin: string;
  totalTransAmount: number;
  chargeDetails: any;
  cashData: any;
  transBranch: string;
  loanForm: FormGroup;
  currencyCode: any;
  showzero: boolean=true;
  paymentMode:any='cash';
 

  subscriptions: Subscription[] = [];

  get value(): any {
    return this.loanForm.value;
  }

  set value(value: any) {
    this.loanForm.setValue(value);
    this.onChange(value);
    this.onTouched();
  }

  get emailControl() {
    return this.loanForm.controls.email;
  }

  constructor(private apiService:ApiService) { }

  ngOnInit(): void {
    this.paymentForm = new FormGroup({
      paymentMode: new FormControl('cash', [Validators.required]),
      transactionCurrency: new FormControl('cash', [Validators.required]),
      loanRepaymentAmount: new FormControl('cash', [Validators.required]),
      negotiatedRate: new FormControl('cash', [Validators.required]),
      totalChargeAmount: new FormControl('cash', [Validators.required]),
      totalTransactionAmount: new FormControl('cash', [Validators.required]),
      exchangeRate :new FormControl('cash', [Validators.required]),
      accountNumber:new FormControl('cash', [Validators.required]),
      accountBalance:new FormControl('cash', [Validators.required]),
      holderName:new FormControl('cash', [Validators.required]),
      accountBranch:new FormControl('cash', [Validators.required]),
      chequeNumber:new FormControl('cash', [Validators.required]),
      
    });
    this.qrData = JSON.parse(JSON.parse(localStorage.getItem('scannerData')));
    setTimeout(() => {
      if (this.qrData != null) {
        console.log('Account', this.qrData);
        this.hideDetails=true;
        this.hideAccNum=true;
        this.showzero = false
        this.currencyCode=this.qrData.accountCurrency;
        this.userName=this.qrData.holderName;
        this.accountBal=this.qrData.accountBalance;
        
      } 
    }, 1000);
  
    this.custValidation = localStorage.getItem('customerValidation');
    this.custNameFromLogin = localStorage.getItem('FromLogin');
    
    if (this.custValidation == 'New Customer' || this.custNameFromLogin != null) {
      this.hideAccNum = false;
      this.showAccountNum = false;
      this.showAccoutType = false;
    }
    if (this.appointmentId) {
      if (this.appointmentId.data.appointmentId) {
        // this.cashWithdrawlData();
      }
    }
    if (
      this.phoneNumber &&
      this.custNameFromLogin == null &&
      this.appointmentId == null
    ) {
      this.ifQRvariable = false;
      this.hideAccNum = true;
      this.showAccountNum = true;
      this.showAccoutType = true;
      this.showzero = false
      console.log('New Customer');
      // this.gettingallTansactionDetails(this.phoneNumber);
    }
  
    this.getCountrynameValues();
    this.getAccountBranch();
    // this.gettransactionBranch('account_branch');
    // this.getBankInformation('KN');

    this.subscriptions.push(
      // this.loanForm.valueChanges.subscribe((value) => {
      //   this.onChange(value);
      //   this.onTouched();
      // })
    );
  }
  getAccountBranch() {
    this.apiService.getBranchforKiosk().subscribe((data: any) => {
      this.branchData = data;
      console.log(this.branchData);
      data.forEach((element, index) => {
        this.branch = data[index].branchName;
        // console.log(this.branch);
      });
    });
  }

  getCountrynameValues() {
    this.apiService.getCountryValues().subscribe((allCountryValues: any) => {
      this.countryValues = allCountryValues;
      console.log(this.countryValues);
    });
  }
  changeTransType(event){
    console.log(event);
    this.paymentMode = event;
    this.checked = !this.checked;
      }
      ngOnDestroy() {
        this.subscriptions.forEach((s) => s.unsubscribe());
      }
    
      onChange: any = () => {};
      onTouched: any = () => {};
    
      registerOnChange(fn) {
        this.onChange = fn;
      }
    
      writeValue(value) {
        if (value) {
          this.value = value;
        }
    
        if (value === null) {
          this.loanForm.reset();
        }
      }
    
      registerOnTouched(fn) {
        this.onTouched = fn;
      }
    
      validate(_: FormControl) {
        return this.loanForm.valid ? null : { profile: { valid: false } };
      }
      addCurrencyImage(currency) {
        return currency.toLowerCase();
      }
}
